      if(this.mail===""){
        return;
      }
      if(this.nowIndex!==-1){//修改的
        this.arrData[this.nowIndex].html = this.content;
        let title = '';

          var r = /#.*?#/;
          var a =this.text.match(r);
          if(!a ){

            a =[this.text.slice(0,25)];
          }
          a[0]=a[0].replace("#","");
          if(!a)a =['图片'];
        this.arrData[this.nowIndex].title  = a[0];

        let data  = {html:this.content,


          userId:this.mail,
          memo_id:   this.arrData[this.nowIndex].memo_id,
          picNum : this.arrData[this.nowIndex].picNum,
          contents:  this.arrData[this.nowIndex].text,
          imageNames:  this.arrData[this.nowIndex].imageNames,
          textContent:  this.arrData[this.nowIndex].text,
          headerIcon:  this.arrData[this.nowIndex].headerIcon,
          memo_time:  this.arrData[this.nowIndex].memo_time,
          updateTime:this.getNewDate(),

          members:  this.arrData[this.nowIndex].members,
          location:  this.arrData[this.nowIndex].location,
          voiceNum:  this.arrData[this.nowIndex].voiceNum,
        };

        this.$http.post('/jinjiapi/updateBeiwanglu', data).then(res => {
          res = res.body;
        console.log(res);

